function userRoles(roles) {
    if(roles == undefined) return;
    const userRolesDiv = document.getElementsByClassName('username-roles');
    for (let i = 0; i < userRolesDiv.length; i++) {
        const div = userRolesDiv[i];

        for (let i = 0; i < roles.length; i++) {
            if(roles[i] == 1) div.innerHTML += '<i class="fas fa-check-circle" data-title="V.I.P."></i>';
            if(roles[i] == 2) div.innerHTML += '<i class="fas fa-user-shield" data-title="Moderátor"></i>';
            if(roles[i] == 3) div.innerHTML += '<i class="fas fa-hammer" data-title="Adminisztátor"></i>';
            if(roles[i] == 4) div.innerHTML += '<i class="fas fa-pencil-paintbrush" data-title="Dizájner"></i>';
            if(roles[i] == 5) div.innerHTML += '<i class="fas fa-code" data-title="Fejlesztő"></i>';
            if(roles[i] == 6) div.innerHTML += '<i class="fas fa-crown" data-title="Tulajdonos"></i>';
        }
    }
}

function setCookie(cName, cValue, expDays) {
    let date = new Date();
    date.setTime(date.getTime() + (expDays * 24 * 60 * 60 * 1000));
    const expires = 'expires=' + date.toUTCString();
    document.cookie = cName + '=' + cValue + '; ' + expires + `; path=/; domain=${process.env.VUE_APP_COOKIE_DOMAIN}`;
}

function getCookie(cName) {
    const name = cName + '=';
    const cDecoded = decodeURIComponent(document.cookie);
    const cArr = cDecoded.split('; ');
    let res;
    cArr.forEach(val => {
        if (val.indexOf(name) === 0) res = val.substring(name.length);
    });
    return res;
}

function deleteCookie(cName) {
    document.cookie = cName + `=; path=/; expires=thu, 01 jan 1970 00:00:01 GMT; domain=${process.env.VUE_APP_COOKIE_DOMAIN}`;
}

exports.userRoles = userRoles;
exports.setCookie = setCookie;
exports.getCookie = getCookie;
exports.deleteCookie = deleteCookie;