<template>
  <router-view/>
  <Notifications/>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import Notifications from '@/components/Notifications';
export default {
  name: 'App',

  components: {
    Notifications,
  },

  computed: {
    ...mapGetters(['testError']),
  },

  methods: {
    ...mapActions(['testAPI', 'checkBan', 'addNotification']),

    console() {
      console.log('%cVárj!', 'color: blue; font-size: 100px;');
      console.log('%cHa valaki azt mondta, másolj be ide valamit, akkor lehet, hogy át akar verni.', 'font-size: 20px;');
      console.log('%cBemásolva ide bármit is, hozzáférést adhatsz a hackereknek a Wolimby fiókodhoz!', 'color: red; font-size: 20px;');
      setInterval(this.console, 300000);
    }
  },

  mounted() {
    this.testAPI().then(res => {
      if(res == undefined) {
        const notification = {
          message: 'A Wolimby Account szerverei alszanak. (500)',
          type: 'information'
        }

        this.addNotification(notification);
      }
    });

    this.checkBan().then(res => {
      if(res.data.banned) {
        this.$router.push('/banned');
      }
    });

    this.console();
  },
  
  created() {
    document.documentElement.setAttribute('data-theme', 'theme1');
  }
}
</script>
