import axios from 'axios';

const state = {
    error: null,
};

const getters = {
    testError: state => state.error,
};

const actions = {
    async testAPI({ commit }) {
        try {
            commit('testRequest');
            const res = await axios.get(`${process.env.VUE_APP_API}/testing/testrequest`);
            if(res.data.success) {
                commit('testSuccess');
            }
            return res;
        } catch(err) {
            commit('testError', err);
        }
    }
};

const mutations = {
    testRequest(state) {
        state.error = null;
    },
    testSuccess(state) {
        state.error = null;
    },
    testError(state, err) {
        state.error = err.name;
    }
};

export default {
    state,
    actions,
    mutations,
    getters
};