import axios from 'axios';
import { setCookie, getCookie, deleteCookie } from '@/assets/js/functions.js';

const state = {
    token: getCookie('token') || '',
    currentUser: {},
    user: {},
    status: '',
    error: null,
    loading: false,
};

const getters = {
    isLoggedIn: state => !!state.token,
    currentUser: state => state.currentUser,
    user: state => state.user,
    userError: state => state.error,
    loading: state => state.loading,
};

axios.defaults.headers.common['Authorization'] = state.token;

const actions = {
    async login({ commit }, [user, checkbox]) {
        try {
            commit('loginRequest');
            const res = await axios.post(`${process.env.VUE_APP_API}/users/login`, user);
            if (res.data.success) {
                const token = res.data.token;
                const user = res.data.user;
                if(checkbox == true) {
                    setCookie('token', token, 365);
                } else {
                    setCookie('token', token);
                }
                axios.defaults.headers.common['Authorization'] = token;
                commit('loginSuccess', token, user);
            }
            return res;
        } catch (err) {
            commit('loginError', err);
        }
    },

    async register({ commit }, user) {
        try {
            commit('registerRequest');
            const res = await axios.post(`${process.env.VUE_APP_API}/users/register`, user);
            if (res.data.success) {
                commit('registerSuccess', user);
            }
            return res;
        } catch (err) {
            commit('registerError', err);
        }
    },

    async patchUser({ commit }, [userId, option, userBody]) {
        commit('patchUserRequest');
        try {
            const res = await axios.patch(`${process.env.VUE_APP_API}/users/patch/${userId}?patching=${option}`, userBody);
            if (res.data.success) {
                commit('patchUserSuccess');
            }
            return res;
        } catch (err) {
            commit('patchUserError', err);
        }
    },

    async uploadImage({ commit }, image) {
        commit('uploadImageRequest');
        try {
            const res = await axios.post('https://api.imgur.com/3/image/', image, {
                headers: {
                    Authorization: 'Client-ID ' + process.env.VUE_APP_IMGUR_CLIENT_ID
                },
            });
            return res;
        } catch (err) {
            commit('uploadImageError', err);
        }
    },

    async deleteNotifications({ commit }, userId) {
        try {
            commit('notificationsRequest');
            const res = await axios.delete(`${process.env.VUE_APP_API}/users/notifications/delete/${userId}`);
            if(res.data.success) {
                commit('notificationsSuccess');
            }
            return res;
        } catch (err) {
            commit('notificationsError', err);
        }
    },

    async getCurrentUser({ commit }) {
        try {
            commit('currentUserRequest');
            const res = await axios.get(`${process.env.VUE_APP_API}/users/currentuser`);
            if(res.data.success) {
                commit('currentUserSuccess', res.data.user);
            }
            return res;
        } catch (err) {
            commit('currentUserError', err);
        }
    },

    async getUser({ commit }, userParams) {
        try {
            commit('userRequest');
            const res = await axios.get(`${process.env.VUE_APP_API}/users/user/${userParams}`);
            const user = res.data.user;
            if(res.data.success) {
                commit('userSuccess', user);
            }
            return res;
        } catch (err) {
            commit('userError', err);
        }
    },

    async logout({ commit }) {
        deleteCookie('token');
        commit('logout');
        delete axios.defaults.headers.common['Authorization'];
    },

    async checkBan({ commit }) {
        try {
            commit('checkBanRequest');
            const res = await axios.get(`${process.env.VUE_APP_API}/users/checkban`);
            if(res.data.banned) {
                deleteCookie('token');
                commit('logout');
                delete axios.defaults.headers.common['Authorization'];
            }
            return res;
        } catch (err) {
            commit('checkBanError', err)
        }
    },

    async deleteUser({ commit }, userId) {
        try {
            commit('deleteUserRequest');
            const res = await axios.delete(`${process.env.VUE_APP_API}/users/delete/${userId}`);
            const user = res.data.user;
            commit('deleteUserSuccess', user);
            deleteCookie('token');
            commit('logout');
            delete axios.defaults.headers.common['Authorization'];
            return res;
        } catch (err) {
            commit('deleteUserError', err)
        }
    }
};

const mutations = {
    loginRequest(state) {
        state.error = null;
        state.status = 'Töltés.';
        state.loading = true;
    },
    loginSuccess(state, token, user) {
        state.error = null;
        state.token = token;
        state.currentUser = user;
        state.status = 'Siker.';
    },
    loginError(state, err) {
        state.error = err.response.data.message;
        state.loading = false;
    },

    registerRequest(state) {
        state.error = null;
        state.status = 'Töltés.';
    },
    registerSuccess(state) {
        state.error = null;
        state.status = 'Siker.';
    },
    registerError(state, err) {
        state.error = err.response.data.message;
    },

    patchUserRequest(state) {
        state.error = null;
        state.status = 'Töltés';
    },
    patchUserSuccess(state) {
        state.status = 'Siker.';
    },
    patchUserError(state, error) {
        state.error = error.response.data.message;
    },

    patchUserPrivacyRequest(state) {
        state.error = null;
        state.status = 'Töltés';
    },
    patchUserPrivacySuccess(state) {
        state.status = 'Siker.';
    },
    patchUserPrivacyError(state, error) {
        state.error = error.response.data.message;
    },

    patchUserSiteRequest(state) {
        state.error = null;
        state.status = 'Töltés';
    },
    patchUserSiteSuccess(state) {
        state.status = 'Siker.';
    },
    patchUserSiteError(state, error) {
        state.error = error.response.data.message;
    },

    uploadImageRequest(state) {
        state.error = null;
        state.status = 'Töltés';
    },
    uploadImageSuccess(state) {
        state.status = 'Siker.';
    },
    uploadImageError(state, error) {
        state.error = error.response.data.message;
    },

    notificationsRequest(state) {
        state.status = 'Töltés';
        state.loading = true;
    },
    notificationsSuccess(state) {
        state.loading = false;
    },
    notificationsError(state, err) {
        state.error = err.response.data.message;
    },

    currentUserRequest(state) {
        state.status = 'Töltés';
        state.loading = true;
    },
    currentUserSuccess(state, user) {
        state.currentUser = user;
        state.loading = false;
    },
    currentUserError(state, err) {
        state.error = err.response.data.message;
        if(err.response.status != 429) {
            deleteCookie('token');
        }
    },

    userRequest(state) {
        state.status = 'Töltés';
        state.loading = true;
    },
    userSuccess(state, user) {
        state.user = user;
        state.loading = false;
    },
    userError(state, err) {
        state.error = err.response.data.message;
        state.loading = false;
    },

    logout(state) {
        state.error = null;
        state.status = '';
        state.token = '';
        state.currentUser = '';
    },

    checkBanRequest(state) {
        state.error = null;
        state.status = 'Töltés';
    },
    checkBanError(state, error) {
        state.error = error.response.data.message;
    },

    deleteUserRequest(state) {
        state.error = null;
        state.status = 'Töltés';
    },
    deleteUserSuccess(state, user) {
        state.currentUser = user;
    },
    deleteUserError(state, error) {
        state.error = error.response.data.message;
    }
};

export default {
    state,
    actions,
    mutations,
    getters
};