const state = {
    notifications: [],
};

const getters = {
    notifications: state => state.notifications
}

const actions = {
    addNotification({ commit }, notification) {
        commit('pushNotification', notification);
        const audio = new Audio('https://notificationsounds.com/storage/sounds/file-sounds-1150-pristine.mp3');
        audio.play();

        setTimeout(function () {
            const notificationId = Math.min(...state.notifications.map(x => x.id));
            commit('deleteNotification', notificationId)
        }, 5000);
    },

    removeNotification({ commit }, notificationToRemove) {
        commit('deleteNotification', notificationToRemove);
    }
};

let nextId = 1;

const mutations = {
    pushNotification(state, notification) {
        state.notifications.push({
            ...notification,
            id: nextId++
        });
    },

    deleteNotification(state, notificationToRemove) {
        state.notifications.splice(state.notifications.map(x => x.id).indexOf(notificationToRemove), 1);
    }
};

export default {
    state,
    actions,
    mutations,
    getters,
};